* {
    box-sizing: border-box;
  }

  html,
body {
  min-height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-weight: 600;
    line-height: 1.5;
    font-size: 18px;
    letter-spacing: .1em;
    background: var(--light);
    font-family: Arial, Helvetica, sans-serif;
  }

main {
	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
}

/*typography*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
    font-size: 2.25rem;
  }
  
  h2 {
    font-size: 2rem;
    font-family: 'Abril Fatface';
    color: var(--darker);
  }
  
  h3 {
    font-size: 1.75rem;
  }
  
  h4 {
    font-size: 1.5rem;
  }
  
  h5 {
    font-size: 1.25rem;
  }
  
  h6 {
    font-size: 1rem;
  }
  
  p {
    margin-top: 0;
  }
  
  a {
    color: var(--tertiary);
    text-decoration: none;
    font-weight: bold;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  header {
    background: var(--dark);
  }
  
  header a {
    color: var(--light);
  }